.header-container{
   height: auto;
   display: flex;
   justify-content: space-between;
   box-shadow: 4px 4px 8px 0px rgba(205, 208, 210, 0.8);
 
 .logo-contaner{
   background-color:transparent;
   justify-content: flex-start;
   } 
    
.butn-container{
    margin-right: 30px;
    justify-content: flex-end;
    cursor: pointer;
    outline: none;
   
             
.buttn-ico{
 border: white;
 background-color:transparent;
